import React, { useEffect } from 'react';
import { Typography, Button, Container } from '@mui/material';
import { Form, Input, Select, message } from 'antd';
import { Link } from 'react-router-dom';
import { useParams } from "react-router-dom";
import axios from 'axios';

import Box from '@mui/material/Box';
import { styled, alpha } from '@mui/material/styles';

import Page from '../../../components/Page';
const { Option } = Select;

const PageTitle = styled('div')(({ theme }) => ({
	backgroundColor: `${alpha(theme.palette.primary.main, 0.08)}`
}));



export default function EcommerceProductList() {
	const [form] = Form.useForm();
	const { id } = useParams();

	const onFinish = (values) => {
		console.log(values)
		if (values.password && !values.confirm) {
			message.config({ top: 100, duration: 5, });
			message.error(`Type your password only if you want to change it.`);
			return;
		}

		axios.post(`${process.env.REACT_APP_SERVER_URL}/updatemailById`, values)
			.then((res) => {
				console.log(res.data)
				message.config({ top: 100, duration: 5, });
				if (res.data.flag === "success") {
					message.success(`You have successfully modified your account.`);
				}
				else if (res.data.flag === 'failed') {
					message.error(res.data.message);
				}
			})
	};

	const onFinishFailed = (errorInfo) => {
		console.log('Failed:', errorInfo);
	};

	useEffect(() => {
		axios.post(`${process.env.REACT_APP_SERVER_URL}/getemailsByid`, { id: id })
			.then((res) => {
				console.log(res.data)
				form.setFieldsValue(res.data.data[0]);
			})
	}, []);

	return (
		<>
			<Page title="Edit Email">
				<PageTitle>
					<Typography
						color="text.primary"
						sx={{
							fontSize: '23px',
							fontWeight: 'bold',
							padding: '10px',
							mb: '20px'
						}}>
						Edit Email
					</Typography>
				</PageTitle>
				<Box>
					<Container>
						<Form
							form={form}
							name="basic"
							labelCol={{
								span: 8,
							}}
							wrapperCol={{
								span: 16,
							}}
							initialValues={{
								remember: true,
							}}
							onFinish={onFinish}
							onFinishFailed={onFinishFailed}
							autoComplete="off"
						>

							<Form.Item
								label="Host"
								name="host"
								rules={[
									{
										required: true,
										message: 'Please input host name',
									},
								]}
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								label="IMAP port"
								name="imap"
								rules={[
									{
										required: true,
										message: 'Please input imap port',
									},
								]}
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								label="SMPT port"
								name="smpt"
								rules={[
									{
										required: true,
										message: 'Please input smpt port',
									},
								]}
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								label="Email"
								name="email"
								rules={[
									{
										type: 'email',
										required: true,
										message: 'Please input email',
									},
								]}
							>
								<Input size="large" />
							</Form.Item>

							<Form.Item
								name="password"
								label="Password"
								rules={[
									{
										required: true,
										message: 'Please input your password',
									},
								]}
								hasFeedback
							>
								<Input.Password size="large" />
							</Form.Item>

							<Form.Item
								name="confirm"
								label="Confirm Password"
								dependencies={['password']}
								hasFeedback
								rules={[
									{
										required: true,
										message: 'Please confirm your password',
									},
									({ getFieldValue }) => ({
										validator(_, value) {
											if (!value || getFieldValue('password') === value) {
												return Promise.resolve();
											}
											return Promise.reject(new Error('The two passwords that you entered do not match'));
										},
									}),
								]}
							>
								<Input.Password size="large" />
							</Form.Item>

							<Form.Item
								label="Status"
								name="status"
								rules={[
									{
										required: true,
										message: 'Please select status',
									},
								]}
							>
								<Select placeholder="Select team" size="large">
									<Option value="1">Active</Option>
									<Option value="0">Disable</Option>
								</Select>
							</Form.Item>

							<Form.Item
								wrapperCol={{
									offset: 8,
									span: 16,
								}}
							>
								<Button sx={{ mx: '15px' }} variant='contained' type="submit">
									Update
								</Button>
								<Link to="/dashboard/esetting">
									<Button variant='outlined'>Cancel</Button>
								</Link>
							</Form.Item>
						</Form>
					</Container>
				</Box>

			</Page>
		</>
	);
}

